header .menu-wrapper {
  position: fixed;
  background-color: #2e6fdd;
  white-space: nowrap;
  text-align: center;
  width: 100%;
  z-index: 3;
}
header .menu-wrapper .custom-menu {
  position: relative;
  padding-top: .5em;
  padding-bottom: .5em;
  display: inline-block;
  width: auto;
  vertical-align: middle;
}
header .menu-wrapper .custom-menu.custom-menu-brand {
  float: left;
  position: absolute;
  left: 0;
}
header .menu-wrapper .custom-menu .menu-item-brand,
header .menu-wrapper .custom-menu .menu-item {
  display: inline-block;
  position: relative;
  padding: .5em 1em;
  text-transform: uppercase;
  color: white;
  font-family: PTSans;
  font-size: 1.5em;
  text-decoration: none;
}
header .menu-wrapper .custom-menu .menu-item-brand:hover,
header .menu-wrapper .custom-menu .menu-item:hover {
  cursor: pointer;
}
header .menu-wrapper .custom-menu .menu-item:hover {
  color: #ddd8ea;
}
