@font-face {
  font-family: "PTSans";
  src: url(/fonts/PTSans.ttc);
}
@font-face {
  font-family: "PTSerif";
  src: url(/fonts/PTSerif.ttc);
}
.home-page-content {
  height: 100%;
}
.home-page-content .fixed-bg {
  position: relative;
  z-index: 1;
  min-height: 100%;
  background-size: cover;
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-position: center center;
}
.home-page-content .fixed-bg .white-mask {
  width: 100%;
  height: 100%;
  position: absolute;
  background-color: rgba(246, 245, 250, 0.9);
}
.home-page-content .fixed-bg.bg-1 {
  background-image: url("/images/code.jpg");
}
.home-page-content .fixed-bg.bg-2 {
  background-image: url("/images/bg2.jpg");
}
.home-page-content .fixed-bg.bg-3 {
  background-image: url("/images/bg3.jpg");
}
.home-page-content .fixed-bg.bg-4 {
  background-image: url("/images/bg4.jpg");
}
@media only screen and (min-width: 1170px) {
  .home-page-content .fixed-bg {
    background-attachment: fixed;
  }
}
.home-page-content h1,
.home-page-content h2 {
  font-family: "Roboto", sans-serif;
  text-align: center;
  font-size: 30px;
  font-size: 1.875rem;
  text-shadow: 0 1px 3px rgba(0, 0, 0, 0.3);
  color: white;
}
.home-page-content h2 {
  position: absolute;
  left: 50%;
  top: 50%;
  bottom: auto;
  right: auto;
  -webkit-transform: translateX(-50%) translateY(-50%);
  -moz-transform: translateX(-50%) translateY(-50%);
  -ms-transform: translateX(-50%) translateY(-50%);
  -o-transform: translateX(-50%) translateY(-50%);
  transform: translateX(-50%) translateY(-50%);
  width: 90%;
  max-width: 1170px;
}
@media only screen and (min-width: 768px) {
  .home-page-content h1,
  .home-page-content h2 {
    font-size: 36px;
  }
}
@media only screen and (min-width: 1170px) {
  .home-page-content h1,
  .home-page-content h2 {
    font-size: 48px;
    font-weight: 300;
  }
}
.home-page-content .scrolling-bg {
  position: relative;
  min-height: 100%;
  z-index: 2;
}
.home-page-content .scrolling-bg.color-1 {
  background-color: #2e6fdd;
}
.home-page-content .scrolling-bg.color-2 {
  background-color: #2b5a88;
}
.home-page-content .scrolling-bg.color-3 {
  background-color: #9b819c;
}
.home-page-content .bg-container {
  position: absolute;
  left: 50%;
  top: 50%;
  bottom: auto;
  right: auto;
  -webkit-transform: translateX(-50%) translateY(-50%);
  -moz-transform: translateX(-50%) translateY(-50%);
  -ms-transform: translateX(-50%) translateY(-50%);
  -o-transform: translateX(-50%) translateY(-50%);
  transform: translateX(-50%) translateY(-50%);
  width: 90%;
  max-width: 1170px;
}
.home-page-content .bg-container.contacts h1 {
  color: white;
  text-shadow: none;
  font-family: PTSerif;
}
.home-page-content .bg-container.under-construction {
  width: 50%;
}
.home-page-content .bg-container.under-construction .under-construction-text-block {
  margin-bottom: 66px;
}
.home-page-content .bg-container.under-construction .under-construction-text-block span {
  color: #333333;
}
.home-page-content .bg-container.under-construction h1 {
  color: #2e6fdd;
  text-shadow: none;
  font-family: PTSerif;
}
